<template>
	<view class="home">
		<!-- #ifdef APP-PLUS -->
<!--		<view class="status_bar">
		    <view class="top_view"></view>
		</view>-->
		<!-- #endif -->
    <!--  title 区域  -->
		<view class="nav">
			<text>DISCOVER</text>
			<text class="iconfont icon-user"></text>
		</view>
    <!--  scroll-content区域  -->
		<scroll-view class="scroll" scroll-y show-scrollbar>
      <!--   search 区域 -->
      <view class="search">
        <text class="iconfont icon-search"></text>
        <input type="text" confirm-type="search" class="search_text">
      </view>
      <!--   swiper 区域  -->
      <swiper class="home_swiper" indicator-dots autoplay circular duration="200"
              indicator-color="#ABA8A8" indicator-active-color="#fff">
        <swiper-item class="swiper_item" v-for="(item, index) in bannerList" :key="index">
          <image :src="item.imageUrl" class="swiper_img"></image>
        </swiper-item>
      </swiper>
      <!--   recommend 区域   -->
      <view class="recommend" v-if="recommend.length > 0">
        <image class="recommend_img1" :src="recommend[0].picUrl" mode="aspectFill"></image>
        <image class="recommend_img2" :src="recommend[1].picUrl" mode="aspectFill"></image>
        <image class="recommend_img3" :src="recommend[2].picUrl" mode="aspectFill"></image>
        <view class="recommend_control">
          <view class="control_text">
            <text class="text1">MOO Radio</text>
            <text class="text2">个性好歌推荐</text>
          </view>
          <text class="control iconfont icon-play" @click="onPlay"
                :class="[getIsPlay ? 'icon-pause' : 'icon-play']"></text>
        </view>
      </view>
      <!--   playlist 区域   -->
      <uni-nav :title="'MOO Playlist_歌单'"></uni-nav>
      <uni-playlist></uni-playlist>
			<ul>
				<li>1</li>
				<li>2</li>
				<li>3</li>
				<li>4</li>
				<li>5</li>
				<li>6</li>
				<li>7</li>
				<li>8</li>
				<li>9</li>
				<li>10</li>
				<li>11</li>
				<li>12</li>
				<li>13</li>
				<li>14</li>
				<li>15</li>
				<li>16</li>
				<li>17</li>
				<li>18</li>
				<li>19</li>
				<li>20</li>
				<li>21</li>
				<li>22</li>
				<li>23</li>
				<li>24</li>
				<li>25</li>
				<li>26</li>
				<li>27</li>
				<li>28</li>
				<li>29</li>
				<li>30</li>
				<li>31</li>
				<li>32</li>
				<li>33</li>
				<li>34</li>
				<li>35</li>
				<li>36</li>
				<li>37</li>
				<li>38</li>
				<li>39</li>
				<li>40</li>
				<li>41</li>
				<li>42</li>
				<li>43</li>
				<li>44</li>
				<li>45</li>
				<li>46</li>
				<li>47</li>
				<li>48</li>
				<li>49</li>
				<li>50</li>
			</ul>
		</scroll-view>
	</view>
</template>

<script>
  import {BASE_URL} from "@/util";
  import {mapMutations, mapState} from 'vuex';
  import types from '@/store/mutations-types';

  import UniPlaylist from "@/components/playlist/Uni-Playlist";
  import UniNav from "@/components/nav/Uni-Nav";

  export default {
		data() {
			return {
				bannerList: [],
        recommend: [],
			};
		},
    components: {
      UniPlaylist,
      UniNav
    },
    created() {
		  this.getBanner();
		  this.getRecommend();
    },
    computed: {
		  ...mapState({
        getIsPlay: 'isPlay'
      }),
    },
    methods: {
		  ...mapMutations([
		     types.IS_PLAY
      ]),
		  getBanner() {
        uni.request({
          url: `${BASE_URL}/banner`,
          success: (res) => {
            this.bannerList = res.data.banners;
            console.log(this.bannerList);
          }
        });
      },
      getRecommend() {
        uni.request({
          url: `${BASE_URL}/personalized?limit=3`,
          success: (res) => {
            this.recommend = res.data.result;
            console.log(this.recommend);
          }
        });
      },
      onPlay() {
		    this[types.IS_PLAY]();
      }
    }
  }
</script>

<style scoped lang="scss">
/*.status_bar {
    height: var(--status-bar-height);
    width: 100%;
    //background-color: $bgc-black2;
}
/*.p_view {
    height: var(--status-bar-height);
    width: 100%;
    position: fixed;
    //background-color: $bgc-black2;
    top: 0;
    //z-index: 999;
}*/
	.home{
		// background-image: linear-gradient( 135deg, #F97794 10%, #623AA2 100%);
    width: 100%;
    height: 100%;
    position: relative;
		//top: 4%;
		background-color: $bgc-black2;
		
		.nav{
			position: absolute;
			top: 4%;
			left: 0;
			right: 0;
			font-size: 70rpx;
      font-weight: bold;
			color: #FFFAFA;
			padding: 0 26rpx 16rpx;
			display: flex;
			justify-content: space-between;
			align-items: center;
			.icon-user{
				font-size: $icon-size;
			}
		}

		.scroll{
			background-color: $bgc-black2;
			position: absolute;
			top: 11%;
			left: 0;
			right: 0;
			bottom: 0;

      .search {
        background-color: $bgc-black1;
        border-radius: 30rpx;
        width: $container-size;
        padding: 6rpx;
        margin: 0 auto 50rpx;
        .icon-search{
          font-size: $icon-size;
          color: $icon-color;
        }
        .search_text {
          display: inline-block;
          width: 88%;
          color: #ffffff;
          caret-color: $icon-color;
          position: relative;
          left: 12rpx;
        }
      }

      .home_swiper{
        width: $container-size;
        margin: 0 auto 50rpx;
        .swiper_item{
          border-radius: 14rpx;
          background-color: grey;
          .swiper_img{
            width: 100%;
            height: 100%;
          }
        }
      }

      .recommend{
        @include wh($container-size, 140rpx);
        position: relative;
        background-color: $bgc-black1;
        border-radius: 60rpx;
        margin: 0 auto 50rpx;
        .recommend_img1{
          @include wh(26%, 100%);
          position: absolute;
          top: 0;
          left: 0;
          border-radius: 8rpx;
        }
        .recommend_img2{
          @include wh(26%, 120%);
          position: absolute;
          left: 10%;
          bottom: 0;
          z-index: 100;
          border-radius: 8rpx;
        }
        .recommend_img3{
          @include wh(26%, 100%);
          position: absolute;
          left: 20%;
          top: 0;
          border-radius: 8rpx;
        }
        .recommend_control{
          @include wh(46%, 84%);
          display: flex;
          justify-content: center;
          align-items: center;
          position: absolute;
          top: 50%;
          left: 58%;
          transform: translate3d(-20%, -50%, 0);
          .control_text{
            @include wh(80%, 100%);
            display: flex;
            align-items: center;
            flex-wrap: wrap;
            .text1{
              color: #fff;
              font-weight: bold;
              font-size: 38rpx;
            }
            .text2{
              color: $font-color;
              font-size: 30rpx;
              letter-spacing: 8rpx;
            }
          }
          .control{
            font-size: 80rpx;
            color: $icon-color;
          }
        }
      }
		}
	}
</style>
